import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Search from '../views/Search.vue'
import Searching from '../views/Searching.vue'
import SearchList from '../views/SearchList.vue'
import Homemale from '../views/Homemale.vue'
import Player from '../views/Player.vue'
import routerr from '../views/routerr.vue'
import read from '../views/read.vue'

Vue.use(VueRouter)

const routes = [{
    path: '/',
    name: 'home',
    component: Home,
  },
  {
    path: '/Homemale',
    name: 'Homemale',
    component: Homemale
  },
  {
    path: '/read',
    name: 'read',
    component: read
  },
  {
    path: '/read',
    name: '-1',
    component: read
  },
  {
    path: '/read',
    name: '0',
    component: read
  },
  {
    path: '/read',
    name: '1',
    component: read
  },
  {
    path: '/read',
    name: '2',
    component: read
  },
  {
    path: '/read',
    name: '3',
    component: read
  },
  {
    path: '/read',
    name: '4',
    component: read
  },
  {
    path: '/read',
    name: '5',
    component: read
  },
  {
    path: '/read',
    name: '6',
    component: read
  },
  {
    path: '/read',
    name: '7',
    component: read
  },
  {
    path: '/read',
    name: '8',
    component: read
  },
  {
    path: '/read',
    name: '9',
    component: read
  },
  {
    path: '/read',
    name: '10',
    component: read
  },
  {
    path: '/routerr',
    name: 'routerr',
    component: routerr
  },
  {
    path: '/Player',
    name: 'Player',
    component: Player
  },
  {
    path: '/search',
    name: 'search',
    component: Search,
    // component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
    children: [{
        path: '/',
        component: SearchList
      },
      {
        path: 'searching',
        component: Searching
      }
    ]
  },

]

const router = new VueRouter({
  routes
})


//路由跳转后，页面回到顶部
router.afterEach(() => {
  document.body.scrollTop = 0;
  document.documentElement.scrollTop = 0;
})

export default router