<template >
  <div class="read">
    <div class="reading" v-for="(item, index) in content" @click.stop="showPopup" :key="index">
      <h1>{{item.title}}</h1>
      <div class="pp">
        <!-- <p v-for="(item, index) in cp" :key="index">&nbsp;&nbsp;&nbsp;{{item}}</p> -->
        <p
          :style="styleMsg"
          v-for="(m, index) in item.cpContent"
          :key="index"
        >&nbsp;&nbsp;&nbsp;{{m}}</p>
      </div>

      <!-- 触底子组件 -->
      <!-- <div class="scroll"></div> -->
      <!-- <Scroll :onBottom="onBottom"></Scroll> -->
    </div>

    <!-- 侧边弹框 -->
    <van-popup
  v-model="showLeft"
  position="left"
  close-on-popstate
  :style="{ height: '100%',width: '70%' }"
><div class="auto">
<p class="title">{{this.$route.query.title}}</p>
<p class="sect">目录</p>
<ul class="listyle">
  <li v-for="(item, index) in section" :key="index" @click="reading(item.link,index)"
  :style='styleActive(index)'
  >
    {{index + 1}}-{{item.title}}
  </li>
</ul>
</div>
 </van-popup>

    <van-popup v-model="show"  position="top" :style="{ height: '7%' }" close-on-popstate>
      <div class="top">
        <van-nav-bar left-text="返回" left-arrow @click-left="onClickLeft">
          <van-icon slot="title">
            <h2>{{this.$route.query.title}}</h2>
          </van-icon>
          <van-icon name="bar-chart-o" slot="right" />
          <van-icon name="user-circle-o" slot="right" />
        </van-nav-bar>
      </div>
      <!-- 底端弹框 -->
    </van-popup>

    <van-popup v-model="show" position="bottom" :style="{ height: '24%' }" close-on-popstate>
      <div class="bottom">
        <div class="slider">
          <van-row >
            
  <van-col span="4"><span>Aa-</span></van-col>
  <van-col span="16"><van-slider v-model="value" :step="25" /></van-col>
  <van-col span="4"><span style="float:right">Aa+</span></van-col>
</van-row>
        </div>
<div class="changeFontSize">
         
  <van-button class="van-button van-button--default van-button--normal van-button--plain active"  @click='white($event,"#eee6dd")' plain>默认</van-button>
  <van-button  @click='white($event,"#0C0C0C")' plain>夜间</van-button>
  <van-button  @click='white($event,"#B8CD8D")' plain>护眼</van-button>

        </div>

        <div class="mostbott">
          <van-row gutter="20">
  <van-col span="8" @click="onTop">上一章</van-col>
  <van-col span="8" @click="getSectionShow">目录</van-col>
  <van-col span="8" @click="clickBottom">下一章</van-col>
</van-row>
        </div>
      </div>
    </van-popup>

  </div>
</template>

<script>

import Vue from "vue";
// 在路由跳转的时候同一个路由多次添加是不被允许的
import Router from 'vue-router'; //如果已引用，不需要重复引用
Vue.use(Router) //如果已引用，不需要重复引用
const VueRouterPush = Router.prototype.push 
Router.prototype.push = function push (to) {
    return VueRouterPush.call(this, to).catch(err => err)
}

export default {
  name: "roll",
  // components:{
  //   Scroll
  // },
  data() {
    return {
      content: [],
      Bokk: null,
      viewone: null,
      readone: null,
      show: false,
      value: 25,
      Bookmodel:null,
      section:{},
      showLeft:false,
      read:null
    };
  },

  computed: {
    // a computed getter
    styleMsg: function() {
      // `this` points to the vm instance
      if (this.value == 0) {
        return {
          "font-size": "16px"
        };
      } else {
        return {
          "font-size": parseInt(this.value / 25) + 17 + "px"
        };
      }
    },
    
  },
  methods: {
    styleActive:function(index){
      if(this.$store.state.count == index){
        return {"color" : "#b93321"}
      }else{
        return null;
      }
    },
    // reading(link,count) {
    //   this.axios
    //     .get("http://novel.kele8.cn/chapters/" + encodeURIComponent(link))
    //     .then(response => {
    //       this.read = response.data.chapter;
    //       this.$store.commit("recount", count);
    //       this.$router.push({
    //         path: "/read",
    //         // name:this.$store.state.count,
    //         query: {
    //           reading: this.read,
    //           bookId: this.$route.query.bookId,
    //           // counts:count,
    //           title:this.$route.query.title,
    //           Link:link
    //         }
    //       });
    //     }).finally(() => {
    //       this.showLeft = false;
    //     });
    // },
     reading(link,count) {
      this.$store.commit("recount", count);
          this.$router.push({
            path: "/read",
            // name:this.$store.state.count,
            query: {
              // reading: this.read,
              bookId: this.$route.query.bookId,
              // counts:count,
              title:this.$route.query.title,
              Link:link
            }
          });
          this.showLeft = false;
    },
    white(e,color){
      if(document.querySelector('.active') == null){
      document.querySelector('.read').style.background = color;
      e.currentTarget.classList.add('active');
      }else{
        document.querySelector('.active').classList.remove('active')
      document.querySelector('.read').style.background = color;
      e.currentTarget.classList.add('active');
      }
      this.show = false
      
    },
    onClickLeft() {
      this.axios
        .get(
          "http://novel.kele8.cn/book-sources?view=summary&book=" +
            this.$route.query.bookId
        )
        .then(response => {
          this.Bookmodel = response.data[0]._id;
          //跳转路由
          this.$router.push({
            path: "/routerr",
            query: {
              model: this.Bookmodel,
              id: this.$route.query.bookId,
              title:this.$route.query.title
            }
          });
        });
    },
    showPopup() {
      this.show = true;
      setTimeout(function() {
        document.getElementsByClassName("van-popup--top")[0].style.zIndex =
          document.getElementsByClassName("van-overlay")[0].style.zIndex + 1;
      }, 0);

      // window.console.log("????")
    },

    deee() {
      var self = this;

      var timer = setTimeout(function() {
        for (var i = 1; i < self.$store.state.timers.length; i++) {
          clearTimeout(self.$store.state.timers[i]);
        }

        //清除保存定时器序号的数组
        self.$store.state.timers = [];

        //获取页面可视高度
        var iw = innerHeight;
        // console.log('iw ==> ', iw);

        //获取滚动距离
        var scrollY = pageYOffset;
        // console.log('scrollY ==> ', scrollY);

        //获取商品最后一个节点
        var last = document.querySelectorAll("p:last-child")[
          document.querySelectorAll("p:last-child").length - 1
        ];
        // console.log('last ==> ', last);

        //获取商品最后一个节点的高度
        var height = parseFloat(getComputedStyle(last).height);
        // console.log('height ==> ', height);

        //获取商品最后一个节点距离页面顶部的距离
        var offsetTop = last.offsetTop;
        // console.log('offsetTop ==> ', offsetTop);

        //距离最后一个商品节点100px左右，触发加载数据
        if (iw + scrollY - height + 30 >= offsetTop) {
          self.onBottom();
        }
      }, 500);

      self.$store.state.timers.push(timer);
    },

    // 防抖
    debounce(fn, wait) {
      var timeout = null;
      return function() {
        if (timeout !== null) clearTimeout(timeout);
        timeout = setTimeout(fn, wait);
      };
    },
    // 侧边弹出
    getSectionShow(){
      this.showLeft = true;
      this.show = false;
    },
    // 加载上一章
    onTop() {
      window.console.log("加载上一章");
      window.removeEventListener("scroll", this.deee);
      if(this.$store.state.count == 0){
        window.console.log('已经是第一章')
        this.show = false
        return;
      }else{
        this.$store.commit("countless");
         this.$router.push({
            
            // name:this.$store.state.count,
            path: "/read",
            query: {
              reading: this.readone,
              bookId: this.$route.query.bookId,
              // counts:this.$route.query.counts + 1,
              title:this.$route.query.title,
              Link:this.$route.query.Link
            }
          });
      }
      
    },
    //获取章节渲染
    getSection(){
        this.axios.get("http://novel.kele8.cn/book-sources?view=summary&book=" +
            this.$route.query.bookId).then(response => {
              this.Bokk = response.data[0]._id;
              this.axios.get("http://novel.kele8.cn/book-chapters/" + this.Bokk)
              .then(response => {
                this.section = response.data.chapters;
              })
            })
    },

    //点击加载下一章
    clickBottom(){
      window.removeEventListener("scroll", this.deee);
      this.$store.commit("countadd");

      // this.getRoute()
      this.$router.push({
            
            // name:this.$store.state.count,
            path: "/read",
            query: {
              reading: this.readone,
              bookId: this.$route.query.bookId,
              // counts:this.$route.query.counts + 1,
              title:this.$route.query.title,
              Link:this.$route.query.Link
            }
          });
       
    },
    getRoute(){
      this.axios
        .get(
          "http://novel.kele8.cn/book-sources?view=summary&book=" +
            this.$route.query.bookId
        )
        .then(response => {
          window.console.log(response.data[0]._id);
          this.Bokk = response.data[0]._id;

          this.axios
            .get("http://novel.kele8.cn/book-chapters/" + this.Bokk)
            .then(response => {
              // window.console.log("111", this.$store.state.count);
              if (this.$store.state.count == undefined) {
                this.viewone = response.data.chapters[0];
              } else {
                this.viewone = response.data.chapters[this.$store.state.count];
              }
              this.axios
                .get(
                  "http://novel.kele8.cn/chapters/" +
                    encodeURIComponent(this.viewone.link)
                )
                .then(response => {
                  this.content = [];
                  this.show = false
                  this.readone = response.data.chapter;
                  this.readone.cpContent = this.readone.cpContent.split("\n");
                  this.content.push(this.readone);
                  window.addEventListener("scroll", this.deee);
                  // this.readone.cpContent = this.readone.cpContent.split("\n");
                  
                }).catch(function(error){
                  window.console.log(error);
                  
                });
            });
        });
    },
    // 触底加载下一章
    onBottom() {
      window.console.log("触底啦！！！");

      this.$store.commit("countadd");

      this.axios
        .get(
          "http://novel.kele8.cn/book-sources?view=summary&book=" +
            this.$route.query.bookId
        )
        .then(response => {
          window.console.log(response.data[0]._id);
          this.Bokk = response.data[0]._id;

          this.axios
            .get("http://novel.kele8.cn/book-chapters/" + this.Bokk)
            .then(response => {
              window.console.log("111", this.$store.state.count);
              if (this.$store.state.count == undefined) {
                this.viewone = response.data.chapters[0];
              } else {
                this.viewone = response.data.chapters[this.$store.state.count];
              }

              // window.console.log("data => ",response.data.chapters[0])
              this.axios
                .get(
                  "http://novel.kele8.cn/chapters/" +
                    encodeURIComponent(this.viewone.link)
                )
                .then(response => {
                  this.readone = response.data.chapter;
                  this.readone.cpContent = this.readone.cpContent.split("\n");
                    this.content.push(this.readone);
                });
            });
        });
    }
  },
  watch: {
    '$route' (){
      window.console.log('页面重新定向')
      this.getRoute();
    }
  },
  created() {
    this.getSection()
  },
  mounted() {
    window.addEventListener("scroll", this.deee);
    // document.querySelector('.van-button:first-child ').classList.add('active');
  },

  destroyed() {
    window.removeEventListener("scroll", this.deee);
  },
  
  
  beforeRouteEnter(to, from, next) {
    Vue.axios.get("http://novel.kele8.cn/chapters/" + encodeURIComponent(to.query.Link))
    .then(response => {
      next(vm => {
        vm.content = [];
      vm.readone = response.data.chapter;
      vm.readone.cpContent = vm.readone.cpContent.split("\n");
      vm.content.push(vm.readone);
      })
      
    })
    // next(vm => {
    //   // vm.path = to.query.reading.title;
    //   // vm.cp = to.query.reading.cpContent.split("\n");
    //   // vm.content = to.query.reading;
    //   // window.console.log(vm.content)

    //   //每次进入 清空content
    //   vm.content = [];
    //   // 章节数清零
    //   // vm.$store.commit("resizecount");
    //   //  获取章节
    //   // vm.$store.commit("recount", to.query.counts);

    //   to.query.reading.cpContent = to.query.reading.cpContent.split("\n");
    //   vm.content.push(to.query.reading);
    // });
  }
};
</script>

<style lang="less" scoped>
.auto{
  
  .title{
    margin: 12px 0 6px 0;
    color: #333;
    padding-left: 17px;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
  }
  .sect{
    color: #999;
    font-size: 14px;
    border-bottom: 1px solid #f2f2f2;
    padding-left: 17px;
    padding-bottom: 5px;
  }
  .listyle{
    
    padding-left: 17px;
    
      
    
  &>li{
    list-style: none;
    height: 40px;
    line-height: 40px;
    color: #999;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    border-bottom: 1px solid #f2f2f2;
    font-size: 12px;
  }
}
}

.top {
  .van-nav-bar__left {
    left: 5px;
  }
  .van-nav-bar {
    background-color: rgba(50, 51, 52, 0.9);
    .van-icon-undefined {
      width: 100%;
      h2 {
        font-size: 14px;
        height: 46px;
        line-height: 40px;
        margin: 0;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
    }
    .van-icon {
      color: #fff;
    }
    .van-nav-bar__text {
      color: #fff;
    }
  }
}
.bottom {
  background-color: rgba(50, 51, 52, 0.9);
  padding: 16px;
  color: white;
  .mostbott{
    margin-top: 20px;
    .van-col{
      text-align: center;
      font-size: 12px;
    }
  }
  .slider{
    .van-slider{
      margin-top: 12px;
    }
  }
  .changeFontSize{
    margin-top: 20px;
    display: flex;
    .active{
      color: #b93221 !important;
      border: 1px solid #b93321 !important;
    }
    .van-button{ 
      background: none;
      flex:1;
      height: 24px;
      line-height: 22px;
      color: white;
      &:first-child {
      margin-right: 15px;
      
    }
    &:nth-child(2) {
      margin-right: 15px;
    }
    }
  }
}
.read {
  color: #5c5d58;
  background: #eee6dd;
  line-height: 1.5;
  h1 {
    padding: 22px 17px;
    padding-bottom: 0;
    font-size: 22px;
    font-weight: 400;
    color: #333;
  }
  .pp {
    padding: 8.5px 17px;
    & > p {
      text-indent: 2em;
      font-size: 18px;
      margin: 5px 0;
    }
  }
}
</style>